html, body {
    width: 100%;
    height: 100dvh;
    overflow-x: hidden;
    overflow-y: hidden;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: #fefefe;
    background: #000;
    scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
a {
    font-family: 'cinzel', sans-serif;
    margin: 0;
}

p,
div {
    font-family: 'bebas', sans-serif;
    font-size: 1.2rem;
}

h1,
h2 {
    text-shadow: #FC0 1px 0 10px;
    font-size: 3rem;
    text-align: center;
    padding: 60px 0 0 0;
}

h3 {
    font-size: 2rem;
}

h4 {
    font-size: 2.5rem;

}